@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@200&display=swap);
*{
    font-family: 'Mulish', sans-serif;
}
.style_error{
    margin-top:15%;
}
.style_error p{
    color:red;
}
.active{
    color:red !important;
    border-bottom: 1px solid red;
}
.active_style{
    text-decoration: none;
    color: black;
    padding-top:15px;
    padding-bottom: 15px;
    font-size: 18px;
}
.heading{
    font-weight: bold;
    color:red !important;
    border-bottom: none;
}
.ml-auto{
    margin-left:auto;
}
.navbar{
    background-color: #fff !important;
}
.header-row{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.header-text{
    margin-top:95px;
    margin-left:auto;
}
.brand-name{
    color:red;
}
.header-button{
    text-decoration: none;
    color:#fff;
    background-color: red;
    padding:10px;
    border-radius:15px;
    font-weight: bold;
}
.header-button:hover{
    background-color: orange;
    color:red;
    border:1px solid black;
}
.place-card{
display:flex;
flex-direction: row;
flex-wrap: wrap;
}
.set-form{
    max-width:60%;
    margin-left:auto;
    margin-right:auto;
}
.footer-style{
    position:fixed;
    bottom:0;
    width:100%;
    left:0;

}
